import { Component, Vue, Watch } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import Util from "../Core/Util";
import { IBaseNode, IDMLNode, LogNodeType } from "../Log";
import Pageable from "../Mixins/Pageable";

interface IRow {
    index: number;
    type: string;
    rows: number;
    duration: number;
    description: string;
    node: IBaseNode;
}

@Component({
    props: {
        root: Object,
        regex: RegExp
    },
    mixins: [Pageable]
})
export default class DMLIndividual extends Vue {
    private root?: IBaseNode;
    private regex?: RegExp;
    private currentPage: number = 1;

    get rows(): IRow[] {
        return (this.root ? Util.findBy(this.root, (x) => x.LogNodeType === LogNodeType.DML) : []).map((n, i) => {
            const dmlNode = n as IDMLNode;
            return {
                index: i + 1,
                type: dmlNode.ObjectType,
                rows: dmlNode.Rows,
                duration: Math.round((n.End - n.Start) / 1000000),
                description: n.Description,
                node: n
            };
        });
    }

    get filteredRows() {
        if (this.regex != null) {
            const regex = this.regex;
            return this.rows.filter((row) => regex.test(row.type));
        }
        return this.rows;
    }

    private selectNode(node: IBaseNode) {
        EventBus.$emit("markNodes", [node]);
    }

    @Watch("regex")
    private onRegexChanged() {
        this.currentPage = 1;
    }
}
