import { Component, Vue } from "vue-property-decorator";

@Component({
    props: {
        bottom: Boolean
    }
})
export default class VerticalSplitter extends Vue {
    public $refs!: {
        root: HTMLElement
    };

    private height: number = 0;
    private totalHeight: number = 0;
    private dividerHeight: number = 6;
    private minimumHeight: number = 20;
    private bottom!: boolean;

    private startHeight: number = 0;
    private startY: number = 0;

    private mouseMoveHandler = this.mouseMove.bind(this);
    private mouseUpHandler = this.mouseUp.bind(this);
    private resizeHandler = this.resize.bind(this);

    get topHeight() {
        return this.bottom ? this.totalHeight - this.dividerHeight - this.height : this.height;
    }

    get bottomHeight() {
        return this.bottom ? this.height : this.totalHeight - this.dividerHeight - this.height;
    }

    private mounted() {
        this.totalHeight = this.$refs.root.clientHeight;
        this.height = (this.totalHeight - this.dividerHeight) / 2;
        window.addEventListener("resize", this.resizeHandler);
    }

    private beforeDestroy() {
        window.removeEventListener("resize", this.resizeHandler);
    }

    private resize(e: UIEvent) {
        this.totalHeight = this.$refs.root.clientHeight;
        if (this.height + this.dividerHeight + this.minimumHeight > this.totalHeight) {
            this.height = Math.max(this.minimumHeight, this.totalHeight - this.dividerHeight - this.minimumHeight);
        }
    }

    private mouseDown(e: MouseEvent) {
        e.preventDefault();
        (document.documentElement as HTMLElement).addEventListener("mousemove", this.mouseMoveHandler);
        (document.documentElement as HTMLElement).addEventListener("mouseup", this.mouseUpHandler);
        this.startY = e.clientY;
        this.startHeight = this.height;
    }

    private mouseUp(e: MouseEvent) {
        (document.documentElement as HTMLElement).removeEventListener("mousemove", this.mouseMoveHandler);
        (document.documentElement as HTMLElement).removeEventListener("mouseup", this.mouseUpHandler);
    }

    private mouseMove(e: MouseEvent) {
        const maximumHeight = this.totalHeight - this.minimumHeight - this.dividerHeight;
        const multiplier = this.bottom ? -1 : 1;
        this.height = Math.max(this.minimumHeight, Math.min(maximumHeight, this.startHeight + multiplier * (e.clientY - this.startY)));
    }
}
