import Util from "../Core/Util";

const PAGE_SIZE = 10;

const mixin = {
    data: function() {
        return {
            sort: null
        };
    },
    computed: {
        sortedRows: function() {
            var sorted = this.filteredRows.slice(0);
            if (this.sort) {
                const direction = this.sort.order === "ascending" ? "" : "-";
                sorted.sort(Util.dynamicSort(direction + this.sort.prop));
            }
            return sorted;
        },
        pagedRows: function() {
            return this.sortedRows.slice((this.currentPage - 1) * PAGE_SIZE, (this.currentPage) * PAGE_SIZE);
        },
        pageCount: function() {
            return Math.ceil(this.sortedRows.length / PAGE_SIZE);
        },
    },
    methods: {
        sortChange: function(column) {
            if (column.prop) {
                this.sort = { prop: column.prop, order: column.order };
            } else {
                this.sort = null;
            }
            this.currentPage = 1;
        }
    }
};
export default mixin;