import { IBaseNode } from "../Log";
import BaseRenderer from "./BaseRenderer";
import GraphComponent from "./GraphComponent";
import Rectangle from "./Rectangle";
import Viewport from "./Viewport";
import ViewportAnimation from "./ViewportAnimation";

function getDepth(node: IBaseNode): number {
    return 1 + node.Children.reduce((max, current) => Math.max(max, getDepth(current)), 0);
}

export default class TimelineRenderer extends BaseRenderer {
    private static SCALE = 0.0001;
    private viewport: Viewport;
    private viewportAnimation?: ViewportAnimation;
    private graphComponent: GraphComponent;
    private depth: number;

    private mouseX: number = 0;
    private mouseY: number = 0;
    private mouseDownHandler: (e: MouseEvent) => void;
    private mouseMoveHandler: (e: MouseEvent) => void;

    public constructor(canvas: HTMLCanvasElement, root: IBaseNode, font: any) {
        super(canvas);

        this.depth = getDepth(root);

        this.mouseDownHandler = (e) => this.OnMouseDown(e);
        this.mouseMoveHandler = (e) => this.OnMouseMove(e);
        canvas.addEventListener("wheel", (e) => this.OnMouseWheel(e), true);
        canvas.addEventListener("mouseup", (e) => this.OnMouseUp(e), true);
        canvas.addEventListener("mousedown", this.mouseDownHandler, true);

        this.viewport = new Viewport(root.Start, root.End, TimelineRenderer.SCALE);
        this.graphComponent = new GraphComponent(this, this.viewport, root, font);
        this.UpdateViewportBounds();
    }

    public MarkNodes(nodes: IBaseNode[]) {
        this.graphComponent.MarkNodes(nodes);
        const left = nodes.reduce((a, v) => Math.min(a, v.Start), Number.MAX_VALUE);
        const right = nodes.reduce((a, v) => Math.max(a, v.End), Number.MIN_VALUE);
        this.ZoomTo(left, right);
    }

    protected OnResize(): void {
        this.graphComponent.SetRegion(new Rectangle(0, 0, this.Width, this.Height));
        this.UpdateViewportBounds();
        this.Dirty();
    }

    protected Update(): void {
        if (this.viewportAnimation) {
            if (this.viewportAnimation.step(this.viewport)) {
                this.graphComponent.OnViewportChanged();
                this.Dirty();
            } else {
                this.viewportAnimation = undefined;
            }
        }
    }

    protected Draw(): void {
        this.graphComponent.Draw();
    }

    private OnMouseWheel(e: WheelEvent) {
        e.preventDefault();
        const scale = (e.deltaY < 0) ? 0.83333333 : 1.2;
        const mouseRatioX = e.offsetX / this.Width;
        this.viewport.Zoom(scale, mouseRatioX);
        this.graphComponent.OnViewportChanged();
        this.Dirty();
    }

    private OnMouseMove(e: MouseEvent) {
        const viewportDeltaX = -(e.offsetX - this.mouseX) / this.Width * this.viewport.GetWidth();
        this.viewport.Translate(viewportDeltaX, this.mouseY - e.offsetY);
        this.graphComponent.OnViewportChanged();
        this.mouseX = e.offsetX;
        this.mouseY = e.offsetY;
        this.Dirty();
    }

    private OnMouseUp(e: MouseEvent) {
        if (e.button === 0) {
            this.Renderer.domElement.removeEventListener("mousemove", this.mouseMoveHandler, true);
            this.Renderer.domElement.addEventListener("mousedown", this.mouseDownHandler, true);
        }
    }

    private OnMouseDown(e: MouseEvent) {
        if (e.button === 0) {
            this.Renderer.domElement.removeEventListener("mousedown", this.mouseDownHandler, true);
            this.Renderer.domElement.addEventListener("mousemove", this.mouseMoveHandler, true);
            this.mouseX = e.offsetX;
            this.mouseY = e.offsetY;
        }
    }

    private ZoomTo(start: number, end: number) {
        this.viewportAnimation = new ViewportAnimation(
            this.viewport.Left,
            this.viewport.Right,
            start * this.viewport.Scale,
            end * this.viewport.Scale,
            30);
        this.Dirty();
    }

    private UpdateViewportBounds() {
        this.viewport.SetMaxY((this.depth + 1) * 15 - this.Height);
        this.graphComponent.OnViewportChanged();
    }
}
