import { Component, Vue, Watch } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import Util from "../Core/Util";
import { IBaseNode, IDMLNode, LogNodeType } from "../Log";
import Pageable from "../Mixins/Pageable";

interface IRow {
    index: number;
    count: number;
    type: string;
    totalRows: number;
    children: IRowChild[];
}

interface IRowChild {
    index: number;
    node: IBaseNode;
}

@Component({
    props: {
        root: Object,
        regex: RegExp
    },
    mixins: [Pageable]
})
export default class DMLGrouped extends Vue {
    private root?: IBaseNode;
    private regex?: RegExp;
    private currentPage: number = 1;

    get rows(): IRow[] {
        const dml = (this.root ? Util.findBy(this.root, (n) => n.LogNodeType === LogNodeType.DML) : []);
        const groupedDml = Util.group(dml, (n) => (n as IDMLNode).ObjectType);

        return Object.keys(groupedDml).map((x, i) => {
            const nodes = groupedDml[x];
            return {
                index: i + 1,
                count: nodes.length,
                type: x,
                totalRows: nodes.reduce((a, v) => a + (v as IDMLNode).Rows, 0),
                children: nodes.map((n, j) => {
                    return {
                        index: j + 1,
                        node: n
                    };
                })
            };
        });
    }

    get filteredRows() {
        if (this.regex) {
            const regex = this.regex;
            return this.rows.filter((row) => regex.test(row.type));
        }
        return this.rows;
    }

    private markNodes(row: IRow) {
        const nodes = row.children.map((x) => x.node);
        EventBus.$emit("markNodes", nodes);
    }

    @Watch("regex")
    private onRegexChanged() {
        this.currentPage = 1;
    }
}
