import * as THREE from "three";
import { Geometry } from "three";
import { IBaseNode, LogNodeType } from "../Log";
import BaseRenderer from "./BaseRenderer";
import Rectangle from "./Rectangle";
import TextComponent from "./TextComponent";
import Viewport from "./Viewport";

class TickComponent {
    private static GetVisibleTicks(left: number, right: number, scale: number): number[] {
        const result: number[] = [];
        for (let i = Math.ceil(left / scale) * scale; i < right; i += scale) {
            result.push(i);
        }
        return result;
    }

    private static Round125(v: number): number {
        const magnitude: number = Math.floor(Math.log(v) / Math.log(10));
        v = v / Math.pow(10, magnitude);
        if (v <= 1.5) {
            v = 1;
        } else if (v <= 3.5) {
            v = 2;
        } else if (v <= 7.5) {
            v = 5;
        } else {
            v = 10;
        }
        return v * Math.pow(10, magnitude);
    }

    private static GetAxisLabel(scale: number) {
        if (scale >= 1000000000) {
            return Math.round(scale / 1000000000) + " s";
        }
        if (scale >= 1000000) {
            return Math.round(scale / 1000000) + " ms";
        }
        if (scale >= 1000) {
            return Math.round(scale / 1000) + " µs";
        }
        return Math.round(scale) + " ns";
    }

    private graphComponent: GraphComponent;

    private tickData?: {
        mesh: THREE.LineSegments,
        geometry: THREE.Geometry
    };
    private tickMaterial: THREE.LineBasicMaterial;
    private tickText: TextComponent;

    constructor(graphComponent: GraphComponent) {
        this.graphComponent = graphComponent;

        this.tickMaterial = new THREE.LineBasicMaterial({ color: 0xd3d3d3, side: THREE.DoubleSide });
        this.tickText = new TextComponent(this.graphComponent.textScene, this.graphComponent.font.glyphs, 0x808080, this.graphComponent.font.texture);
    }

    public OnViewportChanged() {
        const axisScale = TickComponent.Round125(150 / this.graphComponent.region.Width * this.graphComponent.viewport.GetWidth());
        const visibleTicks = TickComponent.GetVisibleTicks(this.graphComponent.viewport.Left, this.graphComponent.viewport.Right, axisScale);

        if (this.tickData) {
            this.graphComponent.scene.remove(this.tickData.mesh);
            this.tickData.geometry.dispose();
            this.tickData = undefined;
        }
        if (visibleTicks.length > 0) {
            const tickGeometry = new THREE.Geometry();
            for (const tick of visibleTicks) {
                tickGeometry.vertices.push(new THREE.Vector3(tick, this.graphComponent.viewport.Top / GraphComponent.ROW_HEIGHT, 0));
                tickGeometry.vertices.push(
                    new THREE.Vector3(tick, (this.graphComponent.region.Height + this.graphComponent.viewport.Top) / GraphComponent.ROW_HEIGHT, 0));
            }
            const tickMesh = new THREE.LineSegments(tickGeometry, this.tickMaterial);
            this.graphComponent.scene.add(tickMesh);
            this.tickData = {
                mesh: tickMesh,
                geometry: tickGeometry
            };
        }

        const axisLabel = TickComponent.GetAxisLabel(axisScale / this.graphComponent.viewport.Scale);
        this.tickText.Reset();
        for (const tick of visibleTicks) {
            const x = Math.floor((tick - this.graphComponent.viewport.Left) * this.graphComponent.region.Width / this.graphComponent.viewport.GetWidth() +
            this.graphComponent.region.Left);
            this.tickText.AddText(x + 5, this.graphComponent.region.Top, axisLabel);
        }
    }
}

interface IBarColor {
    StartColor: number;
    EndColor: number;
}

class BarComponent {
    private static BAR_TEXT_PADDING = 4;

    private graphComponent: GraphComponent;

    private nodeText: TextComponent;

    constructor(graphComponent: GraphComponent) {
        this.graphComponent = graphComponent;

        const geometry = new THREE.Geometry();
        this.BuildVertices(geometry, this.graphComponent.root);
        const material = new THREE.MeshBasicMaterial({ vertexColors: THREE.VertexColors });
        const mesh = new THREE.Mesh(geometry, material);
        this.graphComponent.scene.add(mesh);

        this.nodeText = new TextComponent(this.graphComponent.textScene, this.graphComponent.font.glyphs, 0x000000, this.graphComponent.font.texture);
    }

    public OnViewportChanged() {
        this.nodeText.Reset();
        this.BuildNodeText(this.graphComponent.root);
    }

    private BuildNodeText(node: IBaseNode): void {
        if (node.End * this.graphComponent.viewport.Scale <= this.graphComponent.viewport.Left) {
            return;
        }

        const screenStart = Math.max(this.graphComponent.viewport.Left, node.Start * this.graphComponent.viewport.Scale);
        const screenEnd = Math.min(this.graphComponent.viewport.Right, node.End * this.graphComponent.viewport.Scale);
        const screenWidth = (screenEnd - screenStart) / this.graphComponent.viewport.GetWidth() * this.graphComponent.region.Width -
            2 * BarComponent.BAR_TEXT_PADDING;

        if (screenWidth < 50) { return; }

        const posX = Math.floor((screenStart - this.graphComponent.viewport.Left) / this.graphComponent.viewport.GetWidth() * this.graphComponent.region.Width) +
            this.graphComponent.region.Left + BarComponent.BAR_TEXT_PADDING;
        const posY = Math.floor((node.Depth + 1) * GraphComponent.ROW_HEIGHT) + this.graphComponent.region.Top;
        this.nodeText.AddText(posX, posY, this.nodeText.Trim(node.Description, screenWidth));

        for (const child of node.Children) {
            this.BuildNodeText(child);
        }
    }

    private BuildVertices(geometry: any, node: IBaseNode): void {
        const barColor = this.GetColor(node.LogNodeType);
        const startColor = new THREE.Color(barColor.StartColor);
        const endColor = new THREE.Color(barColor.EndColor);

        const height = (GraphComponent.ROW_HEIGHT - 1) / GraphComponent.ROW_HEIGHT;

        const vertCount = geometry.vertices.length;
        geometry.vertices.push(new THREE.Vector3(node.Start * this.graphComponent.viewport.Scale, node.Depth + 1, 0));
        geometry.vertices.push(new THREE.Vector3(node.Start * this.graphComponent.viewport.Scale, node.Depth + 1 + height, 0));
        geometry.vertices.push(new THREE.Vector3(node.End * this.graphComponent.viewport.Scale, node.Depth + 1, 0));
        geometry.vertices.push(new THREE.Vector3(node.End * this.graphComponent.viewport.Scale, node.Depth + 1 + height, 0));
        geometry.faces.push(new THREE.Face3(vertCount + 2, vertCount + 0, vertCount + 1, undefined,
            [endColor, startColor, startColor]));
        geometry.faces.push(new THREE.Face3(vertCount + 3, vertCount + 2, vertCount + 1, undefined,
            [endColor, endColor, startColor]));

        for (const child of node.Children) {
            this.BuildVertices(geometry, child);
        }
    }

    private GetColor(logNodeType: LogNodeType): IBarColor {
        const colors: any = {};
        colors[LogNodeType.CodeUnit] = { StartColor: 0x00E600, EndColor: 0x00E600 };
        colors[LogNodeType.Method] = { StartColor: 0xE6E600, EndColor: 0xE6E600 };
        colors[LogNodeType.SOQL] = { StartColor: 0x00FFFF, EndColor: 0x00FFFF };
        colors[LogNodeType.DML] = { StartColor: 0xFF6666, EndColor: 0xFF6666 };
        return colors[logNodeType];
    }
}

class MarkComponent {
    private graphComponent: GraphComponent;

    private nodes: IBaseNode[] = [];

    private markData?: {
        mesh: THREE.Mesh,
        geometry: THREE.Geometry
    };
    private markMaterial: THREE.MeshBasicMaterial;

    constructor(graphComponent: GraphComponent) {
        this.graphComponent = graphComponent;

        this.markMaterial = new THREE.MeshBasicMaterial({ vertexColors: THREE.VertexColors });
    }

    public OnViewportChanged() {
        this.Refresh();
    }

    public MarkNodes(nodes: IBaseNode[]) {
        this.nodes = nodes;
        this.Refresh();
    }

    public Refresh() {
        if (this.markData) {
            this.graphComponent.scene.remove(this.markData.mesh);
            this.markData.geometry.dispose();
            this.markData = undefined;
        }
        if (this.nodes.length > 0) {
            const markGeometry = new THREE.Geometry();
            const markMesh = new THREE.Mesh(markGeometry, this.markMaterial);

            const height = (GraphComponent.ROW_HEIGHT - 1) / GraphComponent.ROW_HEIGHT;
            const color = new THREE.Color(0, 0, 0);

            const halfSize = GraphComponent.ROW_HEIGHT / this.graphComponent.region.Width * this.graphComponent.viewport.GetWidth() / 2;

            for (const node of this.nodes) {
                let start = node.Start * this.graphComponent.viewport.Scale;
                let end = node.End * this.graphComponent.viewport.Scale;
                const center = (start + end) / 2;
                const width = (end - start) / this.graphComponent.viewport.GetWidth() * this.graphComponent.region.Width;

                if (width < GraphComponent.ROW_HEIGHT) {
                    start = center - halfSize;
                    end = center + halfSize;
                }

                const vertCount = markGeometry.vertices.length;
                markGeometry.vertices.push(new THREE.Vector3(start, node.Depth + 1, 0));
                markGeometry.vertices.push(new THREE.Vector3(start, node.Depth + 1 + height / 2, 0));
                markGeometry.vertices.push(new THREE.Vector3(start + halfSize, node.Depth + 1, 0));
                markGeometry.vertices.push(new THREE.Vector3(end, node.Depth + 1 + height, 0));
                markGeometry.vertices.push(new THREE.Vector3(end, node.Depth + 1 + height / 2, 0));
                markGeometry.vertices.push(new THREE.Vector3(end - halfSize, node.Depth + 1 + height, 0));
                markGeometry.faces.push(new THREE.Face3(vertCount + 2, vertCount + 0, vertCount + 1, undefined, [color, color, color]));
                markGeometry.faces.push(new THREE.Face3(vertCount + 5, vertCount + 3, vertCount + 4, undefined, [color, color, color]));
            }

            this.graphComponent.scene.add(markMesh);
            this.markData = {
                mesh: markMesh,
                geometry: markGeometry
            };
        }
    }
}

