export default class GraphComponent {
    public static ROW_HEIGHT = 15;

    public viewport: Viewport;
    public root: IBaseNode;
    public region!: Rectangle;
    public font: any;

    public scene: THREE.Scene;
    public textScene: THREE.Scene;
    // public tooltipScene: THREE.Scene;

    private renderer: BaseRenderer;

    private camera!: THREE.Camera;
    private textCamera!: THREE.Camera;

    private tickComponent: TickComponent;
    private barComponent: BarComponent;
    private markComponent: MarkComponent;

    public constructor(renderer: BaseRenderer, viewport: Viewport, root: IBaseNode, font: any) {
        this.renderer = renderer;
        this.viewport = viewport;
        this.root = root;
        this.font = font;

        this.scene = new THREE.Scene();
        this.textScene = new THREE.Scene();
        // this.tooltipScene = new THREE.Scene();

        this.barComponent = new BarComponent(this);
        this.tickComponent = new TickComponent(this);
        this.markComponent = new MarkComponent(this);
        // new TooltipComponent(this);

        this.SetRegion(new Rectangle(0, 0, this.renderer.Width, this.renderer.Height));
    }

    public Draw(): void {
        this.renderer.Renderer.clear();
        this.renderer.Renderer.clearDepth();
        this.renderer.Renderer.render(this.scene, this.camera);
        this.renderer.Renderer.render(this.textScene, this.textCamera);
        // this.renderer.Renderer.render(this.tooltipScene, this.textCamera);
    }

    public MarkNodes(nodes: IBaseNode[]): void {
        this.markComponent.MarkNodes(nodes);
    }

    public SetRegion(region: Rectangle) {
        this.region = region;
        this.OnViewportChanged();
    }

    public OnViewportChanged(): void {
        this.renderer.Renderer.renderLists.dispose();

        this.CalculateProjectionMatrix();

        this.tickComponent.OnViewportChanged();
        this.barComponent.OnViewportChanged();
        this.markComponent.OnViewportChanged();
    }

    private CalculateProjectionMatrix(): void {
        const perPixelX = this.viewport.GetWidth() / this.region.Width;
        const left = this.viewport.Left - this.region.Left * perPixelX;
        const right = this.viewport.Right + (this.renderer.Width - this.region.Right) * perPixelX;
        const top = this.region.Top + this.viewport.Top / GraphComponent.ROW_HEIGHT;
        const bottom = top + this.renderer.Height / GraphComponent.ROW_HEIGHT;
        this.camera = new THREE.OrthographicCamera(left, right, top, bottom, -1, 1);

        this.textCamera = new THREE.OrthographicCamera(
            0, this.renderer.Width, 0, -this.renderer.Height, -1, 1);
        this.textCamera.position.setY(-this.viewport.Top);
    }
}
