import { Component, Vue, Watch } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import Util from "../Core/Util";
import { IBaseNode, LogNodeType } from "../Log";
import Pageable from "../Mixins/Pageable";

interface IRow {
    index: number;
    description: string;
    children: IRowChild[];
}

interface IRowChild {
    index: number;
    node: IBaseNode;
}

@Component({
    props: {
        root: Object,
        regex: RegExp
    },
    mixins: [Pageable]
})
export default class MethodGrouped extends Vue {
    private root?: IBaseNode;
    private regex?: RegExp;
    private currentPage: number = 1;

    get rows(): IRow[] {
        const methods = (this.root ? Util.findBy(this.root, (x) => x.LogNodeType === LogNodeType.Method) : []);
        const groupedMethods = Util.group(methods, (n) => n.Description);

        return Object.keys(groupedMethods).map((x, i) => {
            const nodes = groupedMethods[x];

            let sum = 0;
            let min = Number.MAX_VALUE;
            let max = Number.MIN_VALUE;
            nodes.forEach((node) => {
                const duration = node.End - node.Start;
                sum += duration;
                min = Math.min(duration, min);
                max = Math.max(duration, max);
            });

            return {
                index: i + 1,
                count: nodes.length,
                average: Math.round(sum / nodes.length / 1000000),
                min: Math.round(min / 1000000),
                max: Math.round(max / 1000000),
                sum: Math.round(sum / 1000000),
                description: nodes[0].Description,
                children: nodes.map((n, j) => {
                    return {
                        index: j + 1,
                        node: n
                    };
                })
            };
        });
    }

    get filteredRows() {
        if (this.regex) {
            const regex = this.regex;
            return this.rows.filter((row) => regex.test(row.description));
        }
        return this.rows;
    }

    private markNodes(row: IRow) {
        const nodes = row.children.map((x) => x.node);
        EventBus.$emit("markNodes", nodes);
    }

    @Watch("regex")
    private onRegexChanged() {
        this.currentPage = 1;
    }
}
