export default class Rectangle {
    public Left: number;
    public Right: number;
    public Top: number;
    public Bottom: number;

    public constructor(public X: number, public Y: number, public Width: number, public Height: number) {
        this.Left = X;
        this.Right = X + Width;
        this.Top = Y;
        this.Bottom = Y + Height;
    }
}
