import * as THREE from "three";

import { Component, Vue } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import { IBaseNode } from "../Log";
import ArialFont from "../Rendering/ArialFont";
import TimelineRenderer from "../Rendering/TimelineRenderer";

@Component({
    props: {
        root: Object
    }
})
export default class FlameGraph extends Vue {
    public $refs!: {
        canvas: HTMLCanvasElement
    };

    private root?: IBaseNode;
    private renderer?: TimelineRenderer;

    private created() {
        EventBus.$on("markNodes", (nodes: IBaseNode[]) => {
            if (this.renderer) {
                this.renderer.MarkNodes(nodes);
            }
        });
    }

    private mounted() {
        if (this.root) {
            const root = this.root;
            new THREE.TextureLoader().load("assets/Arial.png", (texture) => {
                this.renderer = new TimelineRenderer(this.$refs.canvas, root, {
                    texture,
                    glyphs: ArialFont
                });
                this.renderer.Run();
            });
        }
    }
}
