import { Component, Vue, Watch } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import Util from "../Core/Util";
import { IBaseNode, LogNodeType } from "../Log";
import Pageable from "../Mixins/Pageable";

interface IRow {
    index: number;
    node: IBaseNode;
}

@Component({
    props: {
        root: Object,
        regex: RegExp
    },
    mixins: [Pageable]
})
export default class MethodIndividual extends Vue {
    private root?: IBaseNode;
    private regex?: RegExp;
    private currentPage: number = 1;

    get rows(): IRow[] {
        return (this.root ? Util.findBy(this.root, (x) => x.LogNodeType === LogNodeType.Method) : []).map((n, i) => {

            const inclusive = n.End - n.Start;
            const exclusive = inclusive - n.Children.reduce((total, current) => total + (current.End - current.Start), 0);

            return {
                index: i + 1,
                description: n.Description,
                duration: Math.round((n.End - n.Start) / 1000000),
                node: n,
                inclusive: Math.round(inclusive / 1000000),
                exclusive: Math.round(exclusive / 1000000),
            };
        });
    }

    get filteredRows(): IRow[] {
        if (this.regex) {
            const regex = this.regex;
            return this.rows.filter((row) => regex.test(row.node.Description));
        }
        return this.rows;
    }

    private selectNode(node: IBaseNode) {
        EventBus.$emit("markNodes", [node]);
    }

    @Watch("regex")
    private onRegexChanged() {
        this.currentPage = 1;
    }
}
