import { Component, Vue } from "vue-property-decorator";

@Component({
    props: {
        text: String,
        find: String,
        regex: RegExp
    }
})
export default class HighlightedText extends Vue {
    private text!: string;
    private regex?: RegExp;

    private render(createElement: any) {
        if (this.regex) {
            const arr = this.text.split(this.regex);
            const children = arr.map((x, i) => {
                if (i % 2 === 0) {
                    return createElement("span", arr[i]);
                }
                return createElement("span", { class: "highlight" }, arr[i]);
            });
            return createElement("div", children);
        } else {
            return createElement("div", this.text);
        }
    }
}
