import Viewport from "./Viewport";

export default class ViewportAnimation {
    private initialStart: number;
    private initialEnd: number;
    private targetStart: number;
    private targetEnd: number;
    private currentFrame: number;
    private totalFrames: number;

    constructor(initialStart: number, initialEnd: number, targetStart: number, targetEnd: number, totalFrames: number) {
        this.initialStart = initialStart;
        this.initialEnd = initialEnd;
        this.targetStart = targetStart;
        this.targetEnd = targetEnd;
        this.totalFrames = totalFrames;
        this.currentFrame = 0;
    }

    public step(viewport: Viewport): boolean {
        this.currentFrame++;
        if (this.currentFrame > this.totalFrames) {
            return false;
        }
        viewport.Left = this.initialStart + (this.targetStart - this.initialStart) * (this.currentFrame / this.totalFrames);
        viewport.Right = this.initialEnd + (this.targetEnd - this.initialEnd) * (this.currentFrame / this.totalFrames);
        return true;
    }
}
