import { Component, Vue, Watch } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import Util from "../Core/Util";
import { IBaseNode, ISOQLNode, LogNodeType } from "../Log";
import Pageable from "../Mixins/Pageable";

interface IRow {
    index: number;
    children: IRowChild[];
}

interface IRowChild {
    index: number;
    node: IBaseNode;
}

@Component({
    props: {
        root: Object,
        regex: RegExp
    },
    mixins: [Pageable]
})
export default class SOQLGrouped extends Vue {
    private root?: IBaseNode;
    private regex?: RegExp;
    private expandRowKeys: object[] = [];
    private currentPage: number = 1;

    get rows(): IRow[] {
        const queries = (this.root ? Util.findBy(this.root, (x) => x.LogNodeType === LogNodeType.SOQL) : []);
        const groupedQueries = Util.group(queries, (n) => n.Description);

        return Object.keys(groupedQueries).map((x, i) => {
            const nodes = groupedQueries[x];
            return {
                index: i + 1,
                count: nodes.length,
                type: (nodes[0] as ISOQLNode).ObjectType,
                totalRows: nodes.reduce((a, v) => a + (v as ISOQLNode).Rows, 0),
                totalDuration: Math.round(nodes.reduce((a, v) => a + (v.End - v.Start) / 1000000, 0)),
                description: nodes[0].Description,
                children: nodes.map((n, j) => {
                    return {
                        index: j + 1,
                        rows: (n as ISOQLNode).Rows,
                        duration: Math.round((n.End - n.Start) / 1000000),
                        node: n
                    };
                })
            };
        });
    }

    get filteredRows() {
        return this.rows;
    }

    private markNode(node: IBaseNode) {
        EventBus.$emit("markNodes", [node]);
    }

    private markNodes(row: IRow) {
        const nodes = row.children.map((x) => x.node);
        EventBus.$emit("markNodes", nodes);
    }

    @Watch("regex")
    private onRegexChanged() {
        this.currentPage = 1;
    }
}
