import App from "./App.vue";
import DMLGrouped from "./DMLGrouped.vue";
import DMLIndividual from "./DMLIndividual.vue";
import DMLTab from "./DMLTab.vue";
import FlameGraph from "./FlameGraph.vue";
import HighlightedText from "./HighlightedText.vue";
import LogDrop from "./LogDrop.vue";
import MethodGrouped from "./MethodGrouped.vue";
import MethodIndividual from "./MethodIndividual.vue";
import MethodTab from "./MethodTab.vue";
import Pagination from "./Pagination.vue";
import SOQLGrouped from "./SOQLGrouped.vue";
import SOQLIndividual from "./SOQLIndividual.vue";
import SOQLTab from "./SOQLTab.vue";
import TabPanel from "./TabPanel.vue";
import VerticalSplitter from "./VerticalSplitter.vue";

const components: {[key: string]: any} = {
    App,
    DMLGrouped,
    DMLIndividual,
    DMLTab,
    FlameGraph,
    HighlightedText,
    LogDrop,
    MethodGrouped,
    MethodIndividual,
    MethodTab,
    Pagination,
    SOQLGrouped,
    SOQLIndividual,
    SOQLTab,
    TabPanel,
    VerticalSplitter
};

const install = (Vue: any, opts = {}) => {
    for (const key of Object.keys(components)) {
        Vue.component(key, components[key]);
    }
};

const exports = {
    install
};

export default exports;
