import * as THREE from "three";

interface IVector2 {
    X: number;
    Y: number;
}

interface IGlyph {
    X0: number;
    X1: number;
    Y0: number;
    Y1: number;
    U0: number;
    U1: number;
    V0: number;
    V1: number;
    Width: number;
    Height: number;
    XAdvance: number;
}

export default class TextComponent {
    private scene: THREE.Scene;
    private geometry: THREE.Geometry;
    private mesh?: THREE.Mesh;
    private material: THREE.MeshBasicMaterial;

    private glyphs: { [id: number]: IGlyph } = {};

    public constructor(scene: THREE.Scene, font: any, color: number, texture: THREE.Texture) {
        this.scene = scene;
        this.geometry = new THREE.Geometry();
        this.material = new THREE.MeshBasicMaterial({
            map: texture,
            transparent: true,
            color,
            side: THREE.DoubleSide
        });
        this.mesh = new THREE.Mesh(this.geometry, this.material);
        this.scene.add(this.mesh);

        const width = font.common.scaleW;
        const height = font.common.scaleH;
        for (const char of font.chars) {
            this.glyphs[char.id] = {
                X0: char.xoffset,
                X1: char.xoffset + char.width,
                Y0: char.yoffset,
                Y1: char.yoffset + char.height,
                U0: char.x / width,
                U1: (char.x + char.width) / width,
                V0: 1 - char.y / height,
                V1: 1 - (char.y + char.height) / height,
                Width: char.width,
                Height: char.height,
                XAdvance: char.xadvance
            };
        }
    }

    public Measure(str: string): IVector2 {
        let width = 0;
        let height = 0;
        for (let i = 0; i < str.length; i++) {
            const glyph = this.glyphs[str.charCodeAt(i)];
            if (!glyph) { continue; }
            width += glyph.XAdvance;
            height = Math.max(height, glyph.Height);
        }
        const result = { X: width, Y: height };
        return result;
    }

    public AddText(x: number, y: number, str: string): void {
        if (!this.mesh) {
            this.geometry = new THREE.Geometry();
            this.mesh = new THREE.Mesh(this.geometry, this.material);
            this.scene.add(this.mesh);
        }

        let posX = x;
        for (let i = 0; i < str.length; i++) {
            const glyph = this.glyphs[str.charCodeAt(i)];
            if (!glyph) { continue; }

            const vertCount = this.geometry.vertices.length;
            this.geometry.vertices.push(new THREE.Vector3(posX + glyph.X0, -(y + glyph.Y1), 0)); // BL
            this.geometry.vertices.push(new THREE.Vector3(posX + glyph.X0, -(y + glyph.Y0), 0)); // TL
            this.geometry.vertices.push(new THREE.Vector3(posX + glyph.X1, -(y + glyph.Y0), 0)); // TR
            this.geometry.vertices.push(new THREE.Vector3(posX + glyph.X1, -(y + glyph.Y1), 0)); // BR
            this.geometry.faces.push(new THREE.Face3(vertCount + 0, vertCount + 1, vertCount + 2));
            this.geometry.faces.push(new THREE.Face3(vertCount + 0, vertCount + 2, vertCount + 3));
            this.geometry.faceVertexUvs[0].push([
                new THREE.Vector2(glyph.U0, glyph.V1),
                new THREE.Vector2(glyph.U0, glyph.V0),
                new THREE.Vector2(glyph.U1, glyph.V0)]);
            this.geometry.faceVertexUvs[0].push([
                new THREE.Vector2(glyph.U0, glyph.V1),
                new THREE.Vector2(glyph.U1, glyph.V0),
                new THREE.Vector2(glyph.U1, glyph.V1)]);

            posX += glyph.XAdvance;
        }
    }

    public Reset(): void {
        if (this.mesh) {
            this.scene.remove(this.mesh);
            this.mesh = undefined;
            this.geometry.dispose();
        }
    }

    public Trim(str: string, maxWidth: number): string {
        let result = "";
        let incomplete = false;
        let currentWidth = 0;

        for (let i = 0; i < str.length; i++) {
            const charWidth = this.Measure(str.charAt(i)).X;
            if (charWidth + currentWidth <= maxWidth) {
                result += str.charAt(i);
                currentWidth += charWidth;
            } else {
                incomplete = true;
                break;
            }
        }
        if (incomplete) {
            const newMaxWidth = maxWidth - this.Measure("...").X;
            for (let i = result.length - 1; i >= 0; i--) {
                if (currentWidth < newMaxWidth) {
                    break;
                }
                const charWidth = this.Measure(result.charAt(i)).X;
                currentWidth -= charWidth;
                result = result.substr(0, result.length - 1);
            }
            result += "...";
        }
        return result;
    }

    public GetMesh() {
        return this.mesh;
    }
}
