import * as THREE from "three";

function throttle(func: () => void, limit: number): () => void {
    let ran = false;
    let pending = false;
    const callback = () => {
        if (pending) {
            func();
            pending = false;
            window.setTimeout(callback, limit);
        } else {
            ran = false;
        }
    };
    return () => {
        if (!ran) {
            func();
            ran = true;
            window.setTimeout(callback, limit);
        } else {
            pending = true;
        }
    };
}

export default abstract class BaseRenderer {
    public Renderer: THREE.WebGLRenderer;

    public Width: number = 0;
    public Height: number = 0;

    private container: HTMLElement;
    private dirty: boolean = true;

    public constructor(canvas: HTMLCanvasElement) {
        this.container = canvas.parentElement as HTMLElement;

        const throttledResize = throttle(() => this.ResizeHandler(), 250);
        const observer = new MutationObserver(() => throttledResize());
        observer.observe(canvas.parentElement as HTMLElement, { attributes: true, attributeFilter: ["style"] });
        window.addEventListener("resize", () => throttledResize(), true);

        this.UpdateDimensions();

        this.Renderer = new THREE.WebGLRenderer({ canvas });
        this.Renderer.autoClear = false;
        this.Renderer.setClearColor(0xffffff);
        this.Renderer.setSize(this.Width, this.Height);
    }

    public Run(): void {
        this.Step();
    }

    protected abstract Update(): void;

    protected abstract Draw(): void;

    protected abstract OnResize(): void;

    protected Dirty() {
        if (!this.dirty) {
            this.dirty = true;
            requestAnimationFrame(() => this.Step());
        }
    }

    private UpdateDimensions(): boolean {
        const newWidth = this.container.clientWidth;
        const newHeight = this.container.clientHeight;
        if (newWidth !== this.Width || newHeight !== this.Height) {
            this.Width = newWidth;
            this.Height = newHeight;
            return true;
        }
        return false;
    }

    private ResizeHandler(): void {
        if (this.UpdateDimensions()) {
            this.Renderer.setSize(this.Width, this.Height);
            this.OnResize();
        }
    }

    private Step(): void {
        this.dirty = false;
        this.Update();
        this.Draw();
    }
}
