import { IBaseNode } from "../Log";

const util = {
    escapeRegex(text: string) {
        return text.replace(/[.?*+^$[\]\\(){}|-]/g, "\\$&");
    },
    group<T>(items: T[], valueSelector: (item: T) => string): {[key: string]: T[]} {
        return items.reduce((agg: { [key: string]: T[] }, item, index) => {
            const value = valueSelector(item);
            (agg[value] = agg[value] || []).push(item);
            return agg;
        }, {});
    },
    dynamicSort(property: string) {
        let sortOrder = 1;
        if (property[0] === "-") {
            sortOrder = -1;
            property = property.substr(1);
        }
        return (a: any, b: any) => {
            const result = (a[property] < b[property]) ? -1 : (a[property] > b[property]) ? 1 : 0;
            return result * sortOrder;
        };
    },
    findBy(node: IBaseNode, predicate: (node: IBaseNode) => boolean) {
        const result: IBaseNode[] = [];
        const findHelper = (node: IBaseNode, predicate: (node: IBaseNode) => boolean, result: IBaseNode[]) => {
            if (predicate(node)) {
                result.push(node);
            }
            node.Children.forEach((child) => findHelper(child, predicate, result));
        };
        findHelper(node, predicate, result);
        return result;
    }
};
export default util;
