import { IBaseLine, ICodeUnitLine, IDMLOpenLine, IMethodLine, ISOQLCloseLine, ISOQLOpenLine } from "./Lines";
import LogNodeType from "./LogNodeType";
import { IBaseNode, ICodeUnitNode, IDMLNode, IMethodNode, ISOQLNode } from "./Nodes";
import OpenCloseType from "./OpenCloseType";

function GetNanoseconds(split: string[]) {
    if (split.length > 0) {
        const match = split[0].match(/\((\d+)\)/);
        if (match) {
            return Number(match[1]);
        }
    }
    return 0;
}

function GetEnd(open: IBaseLine, close: IBaseLine, children: IBaseNode[]) {
    if (close) {
        return close.Nanoseconds;
    }
    if (children.length > 0) {
        return children[children.length - 1].End;
    }
    return open.Nanoseconds;
}

function BuildBaseLine(lineType: LogNodeType, openCloseType: OpenCloseType, split: string[]): IBaseLine {
    return {
        LineType: lineType,
        OpenCloseType: openCloseType,
        Nanoseconds: GetNanoseconds(split)
    };
}

function BuildBaseNode(open: IBaseLine, close: IBaseLine, description: string, children: IBaseNode[], depth: number): IBaseNode {
    return {
        Start: open.Nanoseconds,
        End: GetEnd(open, close, children),
        Description: description,
        Children: children,
        LogNodeType: open.LineType,
        Depth: depth
    };
}

function BuildSOQLNode(open: ISOQLOpenLine, close: ISOQLCloseLine, children: IBaseNode[], depth: number): ISOQLNode {
    return {
        ...BuildBaseNode(open, close, open.Query, children, depth),
        Query: open.Query,
        ObjectType: open.ObjectType,
        Rows: close.Rows
    };
}

function BuildSOQLOpenLine(split: string[]): ISOQLOpenLine {
    const match = split[4].match(/.*from\s+(\w+)\s/i);
    const objectType = match ? match[1] : "???";

    return {
        ...BuildBaseLine(LogNodeType.SOQL, OpenCloseType.Open, split),
        Query: split[split.length - 1],
        ObjectType: objectType
    };
}

function BuildSOQLCloseLine(split: string[]): ISOQLCloseLine {
    const match = split[3].match(/\d+/);
    const rows = match ? Number(match[0]) : 0;

    return {
        ...BuildBaseLine(LogNodeType.SOQL, OpenCloseType.Close, split),
        Rows: rows
    };
}

function BuildDMLNode(open: IDMLOpenLine, close: IBaseLine, children: IBaseNode[], depth: number): IDMLNode {
    const description = open.Operation + " " + open.ObjectType + " " + open.Rows + " Row(s)";
    return {
        ...BuildBaseNode(open, close, description, children, depth),
        Operation: open.Operation,
        ObjectType: open.ObjectType,
        Rows: open.Rows
    };
}

function BuildDMLOpenLine(split: string[]): IDMLOpenLine {
    const operation = split[3].split(":")[1];
    const objectType = split[4].split(":")[1];
    const rows = parseInt(split[5].split(":")[1], 10);
    return {
        ...BuildBaseLine(LogNodeType.DML, OpenCloseType.Open, split),
        Operation: operation,
        ObjectType: objectType,
        Rows: rows
    };
}

function BuildDMLCloseLine(split: string[]): IBaseLine {
    return BuildBaseLine(LogNodeType.DML, OpenCloseType.Close, split);
}

function BuildMethodNode(open: IMethodLine, close: IMethodLine, children: IBaseNode[], depth: number): IMethodNode {
    return {
        ...BuildBaseNode(open, close, open.MethodName, children, depth),
        MethodName: open.MethodName,
        ShortMethodName: open.ShortMethodName
    };
}

function BuildMethodLine(split: string[], openCloseType: OpenCloseType): IMethodLine {
    const methodName = split[split.length - 1];
    const match = methodName.match(/([^.()]+)(\(|$)/);
    const shortMethodName = match ? match[1] : methodName;

    return {
        ...BuildBaseLine(LogNodeType.Method, openCloseType, split),
        MethodName: methodName,
        ShortMethodName: shortMethodName
    };
}

function BuildMethodOpenLine(split: string[]): IMethodLine {
    return BuildMethodLine(split, OpenCloseType.Open);
}

function BuildMethodCloseLine(split: string[]): IMethodLine {
    return BuildMethodLine(split, OpenCloseType.Close);
}

function BuildCodeUnitNode(open: ICodeUnitLine, close: ICodeUnitLine, children: IBaseNode[], depth: number): ICodeUnitNode {
    return {
        ...BuildBaseNode(open, close, open.CodeUnit, children, depth),
        CodeUnit: open.CodeUnit
    };
}

function BuildCodeUnitOpenLine(split: string[]): ICodeUnitLine {
    return {
        ...BuildBaseLine(LogNodeType.CodeUnit, OpenCloseType.Open, split),
        CodeUnit: split[split.length - 1]
    };
}

function BuildCodeUnitCloseLine(split: string[]): ICodeUnitLine {
    return {
        ...BuildBaseLine(LogNodeType.CodeUnit, OpenCloseType.Close, split),
        CodeUnit: split[split.length - 1]
    };
}

interface ITempNode {
    OpenLine: IBaseLine;
    CloseLine?: IBaseLine;
    Children: ITempNode[];
}

export default class LogReader {
    public static Read(log: string): IBaseNode {
        const lines = log.split(/\r?\n/);
        const rootLine = BuildCodeUnitOpenLine(["ROOT"]);
        const tempRoot = {
            OpenLine: rootLine,
            Children: []
        };
        const stack: ITempNode[] = [tempRoot];

        for (const line of LogReader.ParseLines(lines)) {
            if (line.OpenCloseType === OpenCloseType.Open) {
                const parent = stack[stack.length - 1];
                const tempNode = {
                    OpenLine: line,
                    Children: []
                };
                parent.Children.push(tempNode);
                stack.push(tempNode);
            } else {
                if (!LogReader.closes[line.LineType](stack[stack.length - 1].OpenLine, line)) {
                    continue;
                }
                const tempNode = stack.pop();
                if (tempNode) {
                    tempNode.CloseLine = line;
                } else {
                    throw Error("Stack is unexpectedly empty");
                }
            }
        }

        const buildNode = (tempNode: ITempNode, depth: number) => {
            const nextDepth = depth + 1;
            const children = tempNode.Children.map((c) => buildNode(c, nextDepth));
            const result = LogReader.builder[tempNode.OpenLine.LineType](tempNode.OpenLine, tempNode.CloseLine, children, depth);
            return result;
        };

        const rootNode = buildNode(tempRoot, 0);

        if (rootNode.Children.length > 0) {
            rootNode.Start = rootNode.Children[0].Start;
        }

        return rootNode;
    }

    private static initializers: { [key: string]: (split: string[]) => IBaseLine } = {
        CODE_UNIT_STARTED: BuildCodeUnitOpenLine,
        CODE_UNIT_FINISHED: BuildCodeUnitCloseLine,
        METHOD_ENTRY: BuildMethodOpenLine,
        METHOD_EXIT: BuildMethodCloseLine,
        CONSTRUCTOR_ENTRY: BuildMethodOpenLine,
        CONSTRUCTOR_EXIT: BuildMethodCloseLine,
        SOQL_EXECUTE_BEGIN: BuildSOQLOpenLine,
        SOQL_EXECUTE_END: BuildSOQLCloseLine,
        DML_BEGIN: BuildDMLOpenLine,
        DML_END: BuildDMLCloseLine
    };

    private static closes: { [type: number]: (open: any, close: any) => boolean } = {
        [LogNodeType.SOQL]: (open: ISOQLOpenLine, close: ISOQLCloseLine) => true,
        [LogNodeType.DML]: (open: IDMLOpenLine, close: IBaseLine) => true,
        [LogNodeType.Method]: (open: IMethodLine, close: IMethodLine) =>
            open.ShortMethodName === close.ShortMethodName,
        [LogNodeType.CodeUnit]: (open: ICodeUnitLine, close: ICodeUnitLine) =>
            open.CodeUnit === close.CodeUnit
    };

    private static builder: { [type: number]: (open: any, close: any, children: IBaseNode[], depth: number) => IBaseNode } = {
        [LogNodeType.SOQL]: BuildSOQLNode,
        [LogNodeType.DML]: BuildDMLNode,
        [LogNodeType.Method]: BuildMethodNode,
        [LogNodeType.CodeUnit]: BuildCodeUnitNode
    };

    private static ParseLines(lines: string[]): IBaseLine[] {
        const result: IBaseLine[] = [];
        for (const line of lines) {
            const split: string[] = line.split("|");
            if (split.length < 2) {
                continue;
            }

            const init = LogReader.initializers[split[1]];
            if (init) {
                result.push(init(split));
            }
        }
        return result;
    }
}
