import { Component, Vue } from "vue-property-decorator";
import Util from "../Core/Util";
import { IBaseNode } from "../Log";
import MethodGrouped from "./MethodGrouped.vue";
import MethodIndividual from "./MethodIndividual.vue";

@Component({
    props: {
        root: Object
    }
})
export default class MethodTab extends Vue {
    private root?: IBaseNode;
    private filter: string = "";
    private grouped: boolean = false;

    get regex() {
        return this.filter ? new RegExp("(" + Util.escapeRegex(this.filter) + ")", "i") : null;
    }

    get component() {
        return this.grouped ? MethodGrouped : MethodIndividual;
    }

    get props() {
        return {
            root: this.root,
            regex: this.regex,
        };
    }
}
