import { Component, Vue, Watch } from "vue-property-decorator";
import EventBus from "../Core/EventBus";
import Util from "../Core/Util";
import { IBaseNode, ISOQLNode, LogNodeType } from "../Log";
import Pageable from "../Mixins/Pageable";

interface IRow {
    index: number;
    type: string;
    rows: number;
    duration: number;
    description: string;
    expand: boolean;
    node: IBaseNode;
}

@Component({
    props: {
        root: Object,
        regex: RegExp
    },
    mixins: [Pageable]
})
export default class SOQLIndividual extends Vue {
    private root?: IBaseNode;
    private regex?: RegExp;
    private expandRowKeys: object[] = [];
    private currentPage: number = 1;

    get rows(): IRow[] {
        return (this.root ? Util.findBy(this.root, (x) => x.LogNodeType === LogNodeType.SOQL) : []).map((n, i) => {
            const soqlNode = n as ISOQLNode;

            return {
                index: i + 1,
                type: soqlNode.ObjectType,
                rows: soqlNode.Rows,
                duration: Math.round((n.End - n.Start) / 1000000),
                description: n.Description,
                expand: false,
                node: n
            };
        });
    }

    get filteredRows() {
        this.rows.forEach((x) => x.expand = false);
        if (this.regex) {
            const rows: IRow[] = [];
            const regex = this.regex;
            this.rows.forEach((x) => {
                if (regex.test(x.description)) {
                    x.expand = true;
                    rows.push(x);
                } else if (regex.test(x.type)) {
                    rows.push(x);
                }
            });
            return rows;
        }
        return this.rows;
    }

    get expand() {
        return ((this as any).pagedRows as IRow[]).filter((x) => x.expand).map((x) => x.index);
    }

    private selectNode(node: IBaseNode) {
        EventBus.$emit("markNodes", [node]);
    }

    @Watch("regex")
    private onRegexChanged() {
        this.currentPage = 1;
    }
}
